import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import track1 from './music/1.mp3' ;
import track2 from './music/2.mp3';
import track3 from './music/3.mp3';
import track4 from './music/4.mp3';
import track5 from './music/5.mp3';
import track6 from './music/6.mp3';
import track7 from './music/7.mp3';
import track8 from './music/8.mp3';
import $ from "jquery";
import ReactPlayer from 'react-player';
import { PlayButton, PauseButton } from 'react-player-controls'
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
    playing:false,
    next:0,
    }
    this.play = this.play.bind(this)
    this.stop = this.stop.bind(this)
    this.next = this.next.bind(this)
    this.before = this.before.bind(this)
    this.after = this.after.bind(this)
  }
 
   
  
  play(){
    this.setState({
      playing: true,
  })
}
 stop() {
    this.setState({
      playing: false
    })
  }
   next() {
     this.setState({
       next: this.state.next+1
     })  
  }
before() {
    this.setState({
      next: this.state.next - 1,
  
    })
    
  if (this.state.next<0){
    this.setState({
      next: 0,

    })
  }
  }
  after() {
    this.setState({
      next: this.state.next + 1,

    })

    if (this.state.next < 0) {
      this.setState({
        next: 0,

      })
    }
  }
  render() {
    
    $(window).on('load', function () {
     
       var $svg_anm = $('.p_prldr').find('.svg_anm');
      $svg_anm.fadeOut();
      $('.p_prldr').delay(1500).fadeOut('slow');
    })
    $(document).ready(function(){
     /*
      setTimeout(() => {
ReactDOM.render(<App />, document.getElementById('root'));
      }, 3000);
      */
      

      $('.Chill_control').click(function () {

        $('.Chill_button').css({ 'opacity': '1' })
        $('.Chill_control').fadeOut(2000);
      })
      $('.Chill_button_hide').click(function () {
        $('.Chill_button').css({ 'opacity': '0' })
        $('.Chill_control').fadeIn(2000);
      })
      $('.Chill_button--play').click(function(){
        $('.Chill_button--stop').show()
        $('.Chill_button--play').hide()
      })
      $('.Chill_button--stop').click(function () {
        $('.Chill_button--play').show()
        $('.Chill_button--stop').hide()
      })
        $('.Chill_logo').css({'opacity':'1'})
        $('.Chill_logo').removeClass('Chill_run ')
       setTimeout(() => {
         $('.Chill_autor').css({ 'opacity': '1' })
       }, 5000);
    })
   const TrackList ={
     tracks: [track1, track2, track3, track4, track5, track6, track7, track8]   
   }


console.log(this.state.playing)
    return (
   
      <div className='Chill'>   

      <div className="p_prldr">      
            <span className="svg_anm"></span>
        </div>
       
        <ul className='Chill_logo Chill_run '>
          <li >C</li>
          <li >H</li>
          <li >I</li>
          <li >L</li>
          <li >L</li>
 </ul>  <div>
        <div className='Chill_ball'></div>
        <div className='Chill_ball'></div>
        <div className='Chill_ball'></div>
        <div className='Chill_ball'></div>
  
        </div>
   
        <ReactPlayer width='20%' height='20%' url={TrackList.tracks[this.state.next]} onReady={true} onEnded={this.next} playing={this.state.playing}  />
        <span className='Chill_control'>Controls</span>
        <div className='Chill_button'>
          <div className='Chill_button_hide'></div>
          <button className='Chill_button--play' onClick={this.play}></button>
          <button className='Chill_button--stop' onClick={this.stop}></button>
          <button className='Chill_button--before' onClick={this.before}></button>
          <button className='Chill_button--after' onClick={this.after}></button>
        </div>

        <span className='Chill_autor'>Vladislav Vorobyev</span> 

  </div>
    );
  }
}

export default App;



